import React, { useEffect, useRef, useState } from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Container from "react-bootstrap/Container";
import Alert from "react-bootstrap/Alert";
import Image from "react-bootstrap/Image";
import styles from "../../styles/modules/PostCreateEditForm.module.css";
import appStyles from "../../App.module.css";
import btnStyles from "../../styles/modules/Button.module.css";
import { useHistory, useParams } from "react-router-dom";
import { axiosReq } from "../../api/axiosDefaults";

function PostEditForm() {
  const [errors, setErrors] = useState({});
  const [postData, setPostData] = useState({
    title: "",
    content: "",
    image: "",
  });
  const { title, content, image } = postData;
  const [imageFile, setImageFile] = useState(null);

  const imageInput = useRef(null);
  const history = useHistory();
  const { id } = useParams();

  useEffect(() => {
    const handleMount = async () => {
      try {
        const { data } = await axiosReq.get(`/posts/${id}/`);
        const { title, content, image, is_owner } = data;

        is_owner 
          ? setPostData({ 
              title, 
              content, 
              image: image + "?t=" + new Date().getTime() 
            }) 
          : history.push("/");
      } catch (err) {
        console.log(err);
        history.push("/");
      }
    };

    handleMount();
  }, [history, id]);

  const handleChange = (event) => {
    setPostData({
      ...postData,
      [event.target.name]: event.target.value,
    });
  };

  const handleChangeImage = (event) => {
    if (event.target.files.length) {
      URL.revokeObjectURL(image);
      setImageFile(event.target.files[0]);
      setPostData({
        ...postData,
        image: URL.createObjectURL(event.target.files[0]),
      });
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const formData = new FormData();
    const newErrors = {};
  
    // Title validation
    const trimmedTitle = title.trim();
    if (!trimmedTitle) {
      newErrors.title = ['Title is required and cannot be empty.'];
    } else if (trimmedTitle.length < 3) {
      newErrors.title = ['Title must be at least 3 characters long.'];
    } else if (trimmedTitle.length > 100) {
      newErrors.title = ['Title must not exceed 100 characters.'];
    }
  
    // Content validation
    const trimmedContent = content.trim();
    if (trimmedContent.length === 0) {
      newErrors.content = ['Content is required and cannot be empty.'];
    } else if (trimmedContent.length < 10) {
      newErrors.content = ['Content must be at least 10 characters long.'];
    } else if (trimmedContent.length > 1000) {
      newErrors.content = ['Content must not exceed 1000 characters.'];
    }
  
    // Image validation - only if a new file is selected
    if (imageInput.current.files.length > 0) {
      formData.append("image", imageInput.current.files[0]);
    }
  
    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }
  
    formData.append("title", trimmedTitle);
    formData.append("content", trimmedContent);
    if (imageFile) {
      formData.append("image", imageFile);
    }
  
    try {
      await axiosReq.put(`/posts/${id}/`, formData);
      history.push(`/posts/${id}`, { fromEdit: true });
    } catch (err) {
      console.log(err);
      if (err.response?.status !== 401) {
        setErrors(err.response?.data);
      }
    }
  };

  const textFields = (
    <div className="text-center">
      <Form.Group>
        <Form.Label>Title</Form.Label>
        <Form.Control
          type="text"
          name="title"
          value={title}
          onChange={handleChange}
        />
      </Form.Group>
      {errors?.title?.map((message, idx) => (
        <Alert variant="warning" key={idx}>
          {message}
        </Alert>
      ))}

      <Form.Group>
        <Form.Label>Content</Form.Label>
        <Form.Control
          as="textarea"
          rows={6}
          name="content"
          value={content}
          onChange={handleChange}
        />
      </Form.Group>
      {errors?.content?.map((message, idx) => (
        <Alert variant="warning" key={idx}>
          {message}
        </Alert>
      ))}

      <Button
        className={`${btnStyles.Button} ${btnStyles.Blue}`}
        onClick={() => history.goBack()}
      >
        cancel
      </Button>
      <Button className={`${btnStyles.Button} ${btnStyles.Blue}`} type="submit">
        save
      </Button>
    </div>
  );

  return (
    <Form onSubmit={handleSubmit}>
      <Row>
        <Col className="py-2 p-0 p-md-2" md={7} lg={8}>
          <Container
            className={`${appStyles.Content} ${styles.Container} d-flex flex-column justify-content-center`}
          >
            <h2 className="text-center mb-4">Edit your goal in progress</h2>
            <Form.Group className="text-center">
              <figure onClick={() => imageInput.current.click()} style={{cursor: 'pointer', position: 'relative'}}>
                <Image className={appStyles.Image} src={image} rounded />
                <div className={styles.ImageOverlay}>
                  <span>Click to change image</span>
                </div>
              </figure>

              <Form.File
                id="image-upload"
                accept="image/*"
                onChange={handleChangeImage}
                ref={imageInput}
                style={{ display: 'none' }}
              />
            </Form.Group>
            {errors?.image?.map((message, idx) => (
              <Alert variant="warning" key={idx}>
                {message}
              </Alert>
            ))}

            <div className="d-md-none">{textFields}</div>
          </Container>
        </Col>
        <Col md={5} lg={4} className="d-none d-md-block p-0 p-md-2">
          <Container className={appStyles.Content}>{textFields}</Container>
        </Col>
      </Row>
    </Form>
  );
}

export default PostEditForm;